.main-page {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: darkslategrey;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.color {
    width: 16px;
    height: 14px;
    border-radius: 2px;
}

.swatch {
    margin-left: 5px;
    padding: 2px;
    background: #fff;
    border-radius: 1px;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
    display: inline-block;
    cursor: pointer;
}

.icon {
    width: 40px;
    height: 24px;
}

.popover {
    position: absolute;
    z-index: 2;
    top: -330px;
    left: 0px;
}

.cover {
    position: fixed;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
}

.arrow-icon {
    position: absolute;
    font-size: 19px !important;
    color: #777;
    margin-left: 6px !important;
    margin-top: 5px !important;
}

.instruments {
    margin-bottom: 85px;
    cursor: pointer;
}
.instruments-icon {
    width: 20px;
    height: 20px;
}
.instruments-button {
    width: 100px;
    height: 50px;
    margin: 5px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    box-shadow: inset 0px 0px 29px -17px rgba(0,0,0,0.75);
}

.button-click {
    background: darkcyan;
    -webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
.colors__choose {
    width: 100px;
    height: 50px;
    border-radius: 5px;
    margin: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0px 0px 29px -17px rgba(0,0,0,0.75);

    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
}
.click-color {
    width: 25px;
    height: 25px;
    border-radius: 25px;
    margin: 0 5px;
    cursor: pointer;
}
.click-color:hover {
    box-shadow: 0px 0px 21px 0px rgba(11,38,117,1);
}

#choose-color {
    background: #000000;
    border: darkolivegreen;
}
.pen-size {
    -webkit-appearance: none;
    width: 100px;
    height: 10px;
    border-radius: 3px;
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
}

.colors__change {
    width: 100px;
    height: 50px;
    margin: 5px;
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    box-shadow: inset 0px 0px 29px -17px rgba(0,0,0,0.75);
}

.pen-size::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
}

.pen-size::-moz-range-thumb {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
}

#primary {
    background: #61dafb;
}

#secondary {
    background: blueviolet;
}

.canvas {
    image-rendering: -moz-crisp-edges;
    image-rendering: pixelated;
    height: 512px;
    width: 512px;
    cursor: crosshair;

}
.canvas__32size {
    background:
            repeating-linear-gradient(
                    transparent,transparent 15px, rgba(28,110,164,0.3) 16px
            ),
            repeating-linear-gradient(
                    90deg,
                    transparent,transparent 15px, rgba(28,110,164,0.3) 16px
            );
}
.canvas__64size {
    background:
            repeating-linear-gradient(
                    transparent,transparent 7px, rgba(28,110,164,0.3) 8px
            ),
            repeating-linear-gradient(
                    90deg,
                    transparent,transparent 7px, rgba(28,110,164,0.3) 8px
            );
}
.canvas__96size {
    background:
            repeating-linear-gradient(
                    transparent,transparent 4.333px, rgba(28,110,164,0.3) 5.3333px
            ),
            repeating-linear-gradient(
                    90deg,
                    transparent,transparent 4.3333px, rgba(28,110,164,0.3) 5.3333px
            );
}
.canvas__128size {
    background:
            repeating-linear-gradient(
                    transparent,transparent 3px, rgba(28,110,164,0.3) 4px
            ),
            repeating-linear-gradient(
                    90deg,
                    transparent,transparent 3px, rgba(28,110,164,0.3) 4px
            );
}

#fps-range::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
}

#fps-range::-moz-range-thumb {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
}
#fps-range {
    -webkit-appearance: none;
    width: 100px;
    height: 10px;
    border-radius: 3px;
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
}

.animation-player {
    border: 6px double rgba(28,110,164,0.3);
    border-radius: 10px;
}

